small {
    display: block;
    text-overflow: ellipsis;
    overflow-wrap: break-word;
}

.card-bottom {
    display: block;
    padding: 2px;
    margin: 5px;    
}

.poster-container {
    max-height: 200px;
    overflow: hidden;
}

.card {
    margin-top: 20px;
    margin-bottom: 10px;
    border-radius: 0;
    box-shadow: 1px 1px 7px 0px #c5c5c5;
    height: 90%;
}

.poster {
    height: 400px;
}

.icon-space {
    margin-right: 5px;
    margin-bottom: 5px;
}

.card-body {
    padding-bottom: 10px;
    padding-right: 10px;
}

.delete-btn {
    float: right;
    margin-right: 10px;
}

.edit-btn {
    float:left;
    margin-left: 5px;
}

.edit-btn, .delete-btn {
    padding: 0;
    border: none;
    background: none;
}

.edit-btn:focus {
    outline: 0;   
}

.poster-container:hover {
    max-height: 100%;
    transition: max-height 0.2s ease-in;
}

.poster-container:hover + .card-body {
    display: none;
}



@-webkit-keyframes spinner {
    0% {
        -webkit-transform: translate3d(-50%, -50%, 0) rotate(0deg);
                transform: translate3d(-50%, -50%, 0) rotate(0deg);
    }
    100% {
         -webkit-transform: translate3d(-50%, -50%, 0) rotate(360deg);
                 transform: translate3d(-50%, -50%, 0) rotate(360deg);
    }
}

@keyframes spinner {
    0% {
        -webkit-transform: translate3d(-50%, -50%, 0) rotate(0deg);
                transform: translate3d(-50%, -50%, 0) rotate(0deg);
    }
    100% {
         -webkit-transform: translate3d(-50%, -50%, 0) rotate(360deg);
                 transform: translate3d(-50%, -50%, 0) rotate(360deg);
    }
}

.spinner {
    height: 70vh;
    opacity: 1;
    position: relative;
    transition: opacity linear 0.1s;    
}

.spinner:before {
    -webkit-animation: 0.7s linear infinite spinner;
            animation: 0.7s linear infinite spinner;

    border: solid 3px #eee;
    border-bottom-color: #343a40;
    border-radius: 50%;

    content: "";
    height: 60px;
    width: 60px;
    
    left: 50%;
    opacity: inherit;
    position: absolute;
    top: 50%;
    -webkit-transform: translate3d(-50%, -50%, 0);
            transform: translate3d(-50%, -50%, 0);
    -webkit-transform-origin: center;
            transform-origin: center;
    will-change: transform;
}

.spinner-container {
    text-align: center;
}
.error {
    color: red;
    padding: 3px;
}

.warn {
    color: orange;
    padding: 3px;
}

.icon-space {
    margin-right: 5px;
    margin-bottom: 5px;
}
button {
    cursor:pointer;
}
